.project-card-image {
  max-width: 100%;
  height:auto;
  object-fit: cover;
  object-position:center;
  box-shadow: var(--shadow-image-small);
  z-index: 1;
}

@media screen and (min-width: 60em) {
  .project-card-image {
    max-width: 100%;
    height:10rem;
  }


