    max-width: 60%;
    width:50rem;
    height:20rem;
  }
}

.project-card-main-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex: 1;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.project-card-text {
  margin:.2rem .8rem 1rem;
  padding: 0;
  text-align: center;
}

.project-card-text h4 {
  font-size: 1.2rem;
}

.project-card-text h5 {
  font-size: .8rem;
}

@media screen and (min-width: 60em) {
  .project-card-text-wide {
    text-align: left;
    margin: 0;
  }
}

.project-card-text p {
  font-family: var(--paragraph-font);
  font-size: .7rem;
  margin: 14px 0 0;
  text-align: left;
}

.project-links {
  display: flex;
  flex-direction: column;
}

.project-links a {
  padding: 10px;
  text-align: center;
  font-size: .6rem;
  font-weight:bolder;
  border-radius: 30px;
  margin: .3rem;
  text-decoration: none;
  border: solid .1rem rgba(255, 255, 255, 1);
  color: white;
  background-color: none;
  transition: background-color 350ms ease, color 350ms ease;
}

.project-links a:hover {
  background-color: rgb(10, 49, 61);
  border: solid .1rem var(--color-accent);
  color: var(--color-accent);
  text-shadow: var(--shadow-image-small);
}

.btn-projects {
  width: 40%;
}

@media screen and (min-width: 60em) {
  .project-links-wide a {
    border: solid .1rem rgba(255, 255, 255, 1);
    /* color: rgb(37, 31, 31); */
    background-color: var(--color-accent);
    border: none;
    /* box-shadow: var(--shadow-button); */
    transition: background-color 350ms ease, color 350ms ease, box-shadow 350ms ease;
  }

  .project-links-wide a:hover {
    background-color: var(--color-accent-dark);
    /* box-shadow: var(--shadow-button-hover); */
    color: white;
    border: none;
  }
}

/*-- SECTION: CONTACT --*/
.contact-section {
  display: flex;
  flex-direction: column;
}

.contact-section .section-title {
  color: white;
}

.contact-container {
  width: fit-content;
  padding: 2rem;
  margin: 5rem;
}

.about-textbox-content h2 {
  color: rgb(255, 255, 255);
}

/*-- SECTION: CONNECT --*/
.contact-links-list {
  list-style-type: none;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding:1rem 0px;
  font-size: 1rem;
}

.contact-links-list a {
  text-decoration: none;
  padding: 0px 2rem;
  font-size: 3rem;
  color: white;
  text-shadow: 1px 1px 4px rgba(41, 38, 38, 1);
  transition: all .3s ease-in-out;
}

.contact-links-list a:hover {
  color: var(--color-accent);
}
